/* 加載動畫 */
.loadBox__loadAnimate {
  -moz-border-radius: 20px;
  -webkit-border-radius: 20px;
  border-radius: 20px;
  -moz-animation: loadAnimate--news 1.7s infinite linear;
  -webkit-animation: loadAnimate--news 1.7s infinite linear;
  animation: loadAnimate--news 1.7s infinite linear;
  width: 40px;
  height: 40px;
  margin: 0 auto;
  border: 1px solid #CECECF;
}
.loadBox__loadAnimate:before {
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-transition: all 7.7s ease-in-out;
  -o-transition: all 7.7s ease-in-out;
  -webkit-transition: all 7.7s ease-in-out;
  transition: all 7.7s ease-in-out;
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 10px;
  height: 10px;
  background-color: #000;
}
.loadBox--errorBox {
  text-align: center;
}
.loadBox--errorBox__icon {
  width: 50%;
}
.loadBox--errorBox__icon__newsBox {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=37);
  opacity: 0.37;
}
@media (max-device-width: 767px) and (orientation: landscape) {
  .loadBox--errorBox__icon {
    width: 37%;
  }
}

@-moz-keyframes loadAnimate--news {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #000;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #000;
  }
}
@-webkit-keyframes loadAnimate--news {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #000;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #000;
  }
}
@keyframes loadAnimate--news {
  0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #000;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #000;
  }
}
@-moz-keyframes loadAnimate--oldNews {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #FFF;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #FFF;
  }
}
@-webkit-keyframes loadAnimate--oldNews {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #FFF;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #FFF;
  }
}
@keyframes loadAnimate--oldNews {
  0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    border-color: #FFF;
  }
  50% {
    border-color: #CECECF;
  }
  100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    border-color: #FFF;
  }
}
