@-moz-keyframes loadAnimate--timeMachine {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loadAnimate--timeMachine {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes loadAnimate--timeMachine {
  0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/* 全屏模式 */
.fullscreen {
  width: 10px;
  cursor: pointer;
}
.fullscreen:hover {
  width: 15px;
}
.fullscreen:hover path {
  stroke: #000;
  fill: #000;
}
@media (max-device-width: 1024px), (max-width: 1024px) {
  .fullscreen {
    display: none;
  }
}

/* 全屏模式下樣式 */
:-webkit-full-screen .fullscreen {
  width: 15px;
}
:-webkit-full-screen .fullscreen:hover {
  width: 10px;
}

:-moz-full-screen .fullscreen {
  width: 15px;
}
:-moz-full-screen .fullscreen:hover {
  width: 10px;
}

:full-screen .fullscreen {
  width: 15px;
}
:full-screen .fullscreen:hover {
  width: 10px;
}

