/* ui-view 動畫 */
#view-newsBox [ui-view].ng-enter, #view-newsBox [ui-view].ng-leave {
  position: absolute;
  width: 100%;
  height: 100%;
  -moz-transition: all 0.7s ease-in-out;
  -o-transition: all 0.7s ease-in-out;
  -webkit-transition: all 0.7s ease-in-out;
  transition: all 0.7s ease-in-out;
}
@media (max-device-width: 1024px), (max-width: 1024px) {
  #view-newsBox [ui-view].ng-enter, #view-newsBox [ui-view].ng-leave {
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
}
#view-newsBox [ui-view].ng-enter {
  opacity: 0;
  -moz-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -ms-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -webkit-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
}
#view-newsBox [ui-view].ng-enter-active, #view-newsBox [ui-view].ng-leave {
  opacity: 1;
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
#view-newsBox [ui-view].ng-leave-active {
  opacity: 0;
  -moz-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -ms-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -webkit-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
}
#view-newsBox.rtl [ui-view].ng-enter {
  -moz-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -ms-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -webkit-transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  transform: translate3d(-50%, 0, 0) scale3d(0.7, 0.7, 0.7);
}
#view-newsBox.rtl [ui-view].ng-enter-active, #view-newsBox.rtl [ui-view].ng-leave {
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
#view-newsBox.rtl [ui-view].ng-leave-active {
  -moz-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -ms-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  -webkit-transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
  transform: translate3d(50%, 0, 0) scale3d(0.7, 0.7, 0.7);
}
#view-newsBox.cardFlip [ui-view].ng-enter {
  opacity: 0;
  -moz-transform: rotateY(180deg);
  -ms-transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
#view-newsBox.cardFlip [ui-view].ng-enter-active, #view-newsBox.cardFlip [ui-view].ng-leave {
  opacity: 1;
  -moz-transform: rotateY(0deg);
  -ms-transform: rotateY(0deg);
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
}
#view-newsBox.cardFlip [ui-view].ng-leave-active {
  opacity: 0;
  -moz-transform: rotateY(180deg);
  -ms-transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
